<template>
	<view class="container roomDet">
		<view class="roomTop"></view>
		<view class="roomBox">
			<view class="roomInfo">
				<text class="title">基本信息</text>
				<view class="roomItem itemPading"><text>房间名称：</text><text>{{roomDet.roomCode}}</text></view>
				<view class="room_flex">
					<view class="roomItem"><text>产品类型：</text><text>{{roomDet.prodType}}</text></view>
					<view class="roomItem"><text>户型：</text><text>{{roomDet.houseTypeMark}}</text></view>
				</view>
				<view class="room_flex">
					<view class="roomItem"><text>房间结构：</text><text>{{roomDet.roomStructure}}</text></view>
					<view class="roomItem"><text>朝向：</text><text>{{roomDet.orientation}}</text></view>
				</view>
				<view class="roomItem itemPading"><text>楼层：</text><text>{{roomDet.floorNo}}</text></view>
				<image :src="roomLine" class="roomline" mode=""></image>
			</view>
			
			<view class="roomInfo">
				<text class="title">价格明细</text>
				<view class="room_flex">
					<view class="roomItem"><text>建筑面积：</text><text>{{roomDet.prebuildArea}}m²</text></view>
					<view class="roomItem"><text>套内面积：</text><text>{{roomDet.preinsideArea}}m²</text></view>
				</view>
				<view class="room_flex">
					<view class="roomItem"><text>建筑单价：</text><text>{{roomDet.buildMonoPrice}}元/m²</text></view>
					<view class="roomItem"><text>套内单价：</text><text>{{roomDet.insideMonoPrice}}元/m²</text></view>
				</view>
				<view class="room_flex">
					<view class="roomItem"><text>装修单价：</text><text>{{roomDet.decorateMonoPrice}}元/m²</text></view>
					<view class="roomItem"><text>标准总价：</text><text>{{roomDet.standardTotalPrice}}元</text></view>
				</view>
				<view class="room_flex">
					<view class="roomItem"><text>装修总价：</text><text>{{roomDet.decorateTotalPrice}}元</text></view>
					<view class="roomItem"><text>装修标准：</text><text>{{roomDet.decorateStandard}}</text></view>
				</view>
				<image :src="roomLine" class="roomline" mode=""></image>
			</view>
			
			<view class="markBox"><text class="label">备注：</text><text>{{roomDet.remark}}</text></view>
			<!-- <view class="roomItem"><text>产品类型：</text><text>{{roomDet.prodType}}</text></view>
			<view class="roomItem"><text>房间名称：</text><text>{{roomDet.roomCode}}</text></view>
			<view class="roomItem"><text>户型：</text><text>{{roomDet.houseTypeMark}}</text></view>
			<view class="roomItem"><text>房间结构：</text><text>{{roomDet.roomStructure}}</text></view>
			<view class="roomItem"><text>楼层：</text><text>{{roomDet.floorNo}}</text></view>
			<view class="roomItem"><text>朝向：</text><text>{{roomDet.orientation}}</text></view>
			<view class="roomItem"><text>建筑面积：</text><text>{{roomDet.prebuildArea}}m²</text></view>
			<view class="roomItem"><text>套内面积：</text><text>{{roomDet.preinsideArea}}m²</text></view>
			<view class="roomItem"><text>建筑单价：</text><text>{{roomDet.buildMonoPrice}}元/m²</text></view>
			<view class="roomItem"><text>套内单价：</text><text>{{roomDet.insideMonoPrice}}元/m²</text></view>
			<view class="roomItem"><text>装修单价：</text><text>{{roomDet.decorateMonoPrice}}元/m²</text></view>
			<view class="roomItem"><text>标准总价：</text><text>{{roomDet.standardTotalPrice}}元</text></view>
			<view class="roomItem"><text>装修总价：</text><text>{{roomDet.decorateTotalPrice}}元</text></view>
			<view class="roomItem"><text>装修标准：</text><text>{{roomDet.decorateStandard}}</text></view>
			<view class="roomItem"><text>备注：</text><text>{{roomDet.remark}}</text></view> -->
		</view>
		<view class="footer_flex" v-if="status==3">
			<text @click="goPlan" :class="state!=3?'buleFooter':''">置业计划</text>
			<text @click="saleTap" v-if="state==3">申请锁房</text>
		</view>
	</view>
</template>

<script>
	import httpUtils from '@/utils/httpUtils.js';
	import Tools from '@/utils/tools.js';
	export default {
		data() {
			return {
				roomLine: '/static/salePerson/roomLine.png',
				status:'',
				roomDet:{}, // 房间数据
				roomId:'', // 房间id
				state:'', // 房源状态 3可售 4已售 5销控 6认购
			}
		},
		onLoad(options){
			this.status = options.status
			this.roomId = options.roomId
			this.state = options.state
			this._getDet()
			// this.roomId
		},
		methods:{
			// 获取房间详情
			_getDet(){
				let that = this
				Tools.showLoading('加载中')
				httpUtils.sendHttpRequest('2006', Object.assign({},{roomId:that.roomId}), function (res) {
					Tools.hideLoading()
					if (res.data.code == '0') {
						that.roomDet = res.data.data[0]
					}
				})
			},
			// 申请锁房
			saleTap(){
				let that = this
				Tools.showLoading('锁房中')
				httpUtils.sendHttpRequest('2048', Object.assign({},{roomId:that.roomId}), function (res) {
					Tools.hideLoading()
					if (res.data.code == '0') {
						Tools.showSuccessToast('锁房成功')
						setTimeout(function(){
							const pages = getCurrentPages(); //当前页面栈
							if (pages.length > 1) {
								let beforePage = pages[pages.length - 2]; //获取上一个页面实例对象  
								beforePage.$vm.getList(); //触发父页面中的方法getList()
							 }
							 uni.navigateBack({
							 	delta : 1
							 })
						},500)
					}
				})
			},
			// 置业计划
			goPlan(){
				uni.navigateTo({
					url:'/pages/salemanVisual/more/plan?roomDet=' + encodeURIComponent(JSON.stringify(this.roomDet))
				})
			},
		}
	}
</script>

<style lang="scss">
	page{
		background-color: #f6f6f6;
	}
	.roomDet{
		.roomTop{
			height: 300rpx;
			background: linear-gradient(180deg, #03958C, rgba(3, 149, 140, 0));
		}
		.roomBox{
			background-color: #fff;
			border-radius: 24rpx;
			width: 690rpx;
			position: absolute;
			top: 30rpx;
			left: 30rpx;
			padding: 30rpx 0;
			.roomInfo{
				.title{
					font-size: 36rpx;
					font-weight: bold;
					display: inline-block;
					padding: 0 30rpx 30rpx;
				}
				.roomItem{
					text{
						font-size: 28rpx;
						line-height: 60rpx;
					}
					text:first-child{
						color: #999;
						display: inline-block;
						font-size: 26rpx;
					}
				}
				.itemPading{
					padding: 0 30rpx;
				}
				.room_flex{
					padding: 0 30rpx;
					display: flex;
					flex: 1;
					.roomItem{
						width: 50%;
					}
					.roomItem:last-child{
						padding-left: 10rpx;
					}
				}
				.roomline{
					width: 100%;
					height: 28rpx;
					margin: 30rpx 0;
				}
			}
			.markBox{
				width: 640rpx;
				margin: auto;
				background-color: #F5F5F5;
				padding: 20rpx 10rpx;
				border-radius: 8rpx;
				.label{
					color: #999;
					font-size: 26rpx;
				}
			}
		}
		.footer_flex{
			.buleFooter{
				color: #fff!important;
				background: linear-gradient(90deg, #03958C, #00AE66);
				margin: 0 95rpx;
			}
		}
	}
</style>
